import React, { ChangeEvent, KeyboardEvent } from 'react';
import style from './InputNumber.module.css'

type InputNumberPropsType = {
    callBack: (value: number) => void
    error?: boolean
    value: number
}

export const InputNumber: React.FC<InputNumberPropsType> = React.memo(({callBack, error, value}) => {

    const onChangeHandler = (e: ChangeEvent<HTMLInputElement>) =>
        callBack(isNaN(e.currentTarget.valueAsNumber) ? 0 : Math.trunc(e.currentTarget.valueAsNumber))

    const onKeyDown = (e:KeyboardEvent<HTMLInputElement>) => {
        if (e.key.match(/[.]/)){
            e.preventDefault()
        }
    }

    return <div>
        <input className={error ? style.error : style.input}
               onChange={onChangeHandler}
               onKeyDown={onKeyDown}
               type="number"
               value={value.toFixed()}
        />
    </div>
})